"use client";
import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Lock, Edit2, RotateCcw } from 'lucide-react';

interface TPAllocation {
    tp1: number;
    tp2: number;
    tp3: number;
}

const PRESETS: Record<string, TPAllocation> = {
    conservative: { tp1: 70, tp2: 20, tp3: 10 },
    balanced: { tp1: 50, tp2: 30, tp3: 20 },
    aggressive: { tp1: 30, tp2: 30, tp3: 40 }
};

export const MultiTPSplitter = () => {
    const [locked, setLocked] = useState(true);
    const [allocation, setAllocation] = useState<TPAllocation>(PRESETS.balanced);
    const [isDragging, setIsDragging] = useState<string | null>(null);

    const total = allocation.tp1 + allocation.tp2 + allocation.tp3;
    const isValid = total === 100;

    const handleSliderChange = (tp: keyof TPAllocation, value: number) => {
        if (locked) return;
        setAllocation(prev => ({ ...prev, [tp]: value }));
    };

    const applyPreset = (preset: keyof typeof PRESETS) => {
        setAllocation(PRESETS[preset]);
    };

    const tpConfigs = [
        { key: 'tp1' as const, label: 'TP1', color: 'from-blue-500 to-blue-600', borderColor: 'border-blue-500/30' },
        { key: 'tp2' as const, label: 'TP2', color: 'from-purple-500 to-purple-600', borderColor: 'border-purple-500/30' },
        { key: 'tp3' as const, label: 'TP3', color: 'from-green-500 to-green-600', borderColor: 'border-green-500/30' }
    ];

    return (
        <div className="glass-glamor-heavy rounded-3xl p-6">
            {/* Header */}
            <div className="flex items-center justify-between mb-6">
                <div>
                    <h3 className="text-lg font-bold text-white mb-1">Take Profit Splitter</h3>
                    <p className="text-xs text-gray-500">Position closing allocation</p>
                </div>
                <div className="flex gap-2">
                    <button
                        onClick={() => setLocked(!locked)}
                        className={`flex items-center gap-2 px-3 py-2 rounded-lg text-xs font-bold transition haptic-feedback ${locked
                                ? 'bg-green-500/10 text-green-400 border border-green-500/30 glow-profit'
                                : 'bg-blue-500/10 text-blue-400 border border-blue-500/30'
                            }`}
                    >
                        {locked ? <Lock size={14} /> : <Edit2 size={14} />}
                        {locked ? 'Locked' : 'Editing'}
                    </button>
                </div>
            </div>

            {/* Presets */}
            {!locked && (
                <motion.div
                    initial={{ height: 0, opacity: 0 }}
                    animate={{ height: 'auto', opacity: 1 }}
                    className="mb-6"
                >
                    <div className="text-xs font-bold text-gray-500 uppercase tracking-wider mb-3">Quick Presets</div>
                    <div className="grid grid-cols-3 gap-2">
                        {Object.keys(PRESETS).map((preset) => (
                            <button
                                key={preset}
                                onClick={() => applyPreset(preset as keyof typeof PRESETS)}
                                className="glass-glamor rounded-lg px-3 py-2 text-xs font-bold capitalize hover:bg-white/10 transition haptic-feedback border border-white/10"
                            >
                                {preset}
                            </button>
                        ))}
                    </div>
                </motion.div>
            )}

            {/* TP Sliders */}
            <div className="space-y-6 mb-6">
                {tpConfigs.map((config, i) => {
                    const value = allocation[config.key];
                    return (
                        <motion.div
                            key={config.key}
                            initial={{ opacity: 0, x: -20 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ delay: i * 0.1 }}
                        >
                            <div className="flex items-center justify-between mb-3">
                                <div className="flex items-center gap-3">
                                    <div className={`h-3 w-3 rounded-full bg-gradient-to-r ${config.color}`} />
                                    <span className="text-sm font-bold text-white">{config.label}</span>
                                </div>
                                <div className="flex items-center gap-2">
                                    <span className="text-2xl font-black text-white font-mono">{value}</span>
                                    <span className="text-sm text-gray-500">%</span>
                                </div>
                            </div>

                            <div className="relative">
                                {/* Track */}
                                <div className={`h-3 rounded-full glass-glamor border ${config.borderColor} overflow-hidden`}>
                                    <motion.div
                                        className={`h-full bg-gradient-to-r ${config.color}`}
                                        animate={{ width: `${value}%` }}
                                        transition={{ type: "spring", stiffness: 300, damping: 30 }}
                                    />
                                </div>

                                {/* Slider Input */}
                                {!locked && (
                                    <input
                                        type="range"
                                        min="0"
                                        max="100"
                                        value={value}
                                        onChange={(e) => handleSliderChange(config.key, parseInt(e.target.value))}
                                        onMouseDown={() => setIsDragging(config.key)}
                                        onMouseUp={() => setIsDragging(null)}
                                        className="absolute inset-0 w-full opacity-0 cursor-pointer"
                                    />
                                )}
                            </div>
                        </motion.div>
                    );
                })}
            </div>

            {/* Visual Representation */}
            <div className="mb-6">
                <div className="text-xs font-bold text-gray-500 uppercase tracking-wider mb-3">Position Split</div>
                <div className="h-16 rounded-2xl overflow-hidden flex glass-glamor border border-white/10">
                    {tpConfigs.map((config) => {
                        const value = allocation[config.key];
                        return (
                            <motion.div
                                key={config.key}
                                className={`bg-gradient-to-br ${config.color} flex items-center justify-center`}
                                animate={{ width: `${value}%` }}
                                transition={{ type: "spring", stiffness: 300, damping: 30 }}
                            >
                                {value > 10 && (
                                    <span className="text-white font-bold text-sm">{value}%</span>
                                )}
                            </motion.div>
                        );
                    })}
                </div>
            </div>

            {/* Validation */}
            <div className={`rounded-xl p-4 text-center font-bold transition ${isValid
                    ? 'bg-green-500/10 text-green-400 border border-green-500/30'
                    : 'bg-red-500/10 text-red-400 border border-red-500/30'
                }`}>
                <div className="text-sm mb-1">Total Allocation</div>
                <div className="text-3xl font-black font-mono">{total}%</div>
                {!isValid && <div className="text-xs mt-2 text-red-300">Must equal 100%</div>}
            </div>
        </div>
    );
};
