"use client";
import React, { useEffect } from 'react';
import { motion } from 'framer-motion';
import { SentinelAI } from '@/components/trading/glamorism/SentinelAI';
import { MultiTPSplitter } from '@/components/trading/glamorism/MultiTPSplitter';
import { ExecutionPrison } from '@/components/trading/glamorism/ExecutionPrison';
import { TopTierLeaderboard } from '@/components/trading/glamorism/TopTierLeaderboard';
import { SyncStatusIndicator } from '@/components/trading/glamorism/SyncStatusIndicator';
import { useTradingStore } from '@/stores/useTradingStore';
import '@/styles/glamorism.css';

const GlamorismDashboard = () => {
    const { isConnected, latency, setConnectionStatus } = useTradingStore();

    // Poll for MT5 connection status
    useEffect(() => {
        const checkConnection = async () => {
            try {
                const res = await fetch('http://localhost:8000/verify-connection');
                const data = await res.json();
                setConnectionStatus(true, 18); // Mock latency
            } catch (e) {
                setConnectionStatus(false, 0);
            }
        };

        checkConnection();
        const interval = setInterval(checkConnection, 5000);
        return () => clearInterval(interval);
    }, [setConnectionStatus]);

    return (
        <div className="min-h-screen bg-black text-white p-6">
            {/* Ambient Background */}
            <div className="fixed top-0 left-0 w-full h-full -z-10 overflow-hidden">
                <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-blue-600/10 rounded-full blur-[150px] animate-pulse" />
                <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-purple-600/10 rounded-full blur-[150px] animate-pulse" style={{ animationDelay: '1s' }} />
            </div>

            {/* Header */}
            <motion.div
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-6"
            >
                <div className="flex items-center justify-between mb-4">
                    <div>
                        <h1 className="text-4xl font-black bg-clip-text text-transparent bg-gradient-to-r from-blue-400 via-purple-400 to-pink-400">
                            Verstige Command Center
                        </h1>
                        <p className="text-gray-500 mt-1">Glamorism Trading Dashboard</p>
                    </div>
                    <SyncStatusIndicator isConnected={isConnected} latency={latency} />
                </div>
            </motion.div>

            {/* Command Grid */}
            <div className="command-grid">
                {/* Left Sidebar - Sentinel AI */}
                <div className="h-full">
                    <SentinelAI />
                </div>

                {/* Center - Main Trading Area */}
                <div className="flex flex-col gap-6 overflow-y-auto custom-scrollbar">
                    {/* Chart Placeholder */}
                    <motion.div
                        initial={{ opacity: 0, scale: 0.95 }}
                        animate={{ opacity: 1, scale: 1 }}
                        transition={{ delay: 0.2 }}
                        className="glass-glamor-heavy rounded-3xl p-6 h-[500px]"
                    >
                        <div className="flex items-center justify-between mb-4">
                            <h3 className="text-lg font-bold text-white">Live Chart</h3>
                            <div className="flex gap-2">
                                {['1M', '5M', '15M', '1H', '4H', '1D'].map((tf) => (
                                    <button
                                        key={tf}
                                        className="px-3 py-1 rounded-lg glass-glamor text-xs font-bold hover:bg-white/10 transition"
                                    >
                                        {tf}
                                    </button>
                                ))}
                            </div>
                        </div>
                        <div className="h-full flex items-center justify-center text-gray-600">
                            <div className="text-center">
                                <div className="text-6xl mb-4"></div>
                                <p className="text-sm">TradingView Chart Integration</p>
                                <p className="text-xs text-gray-700 mt-2">Lightweight Charts API</p>
                            </div>
                        </div>
                    </motion.div>

                    {/* Multi-TP Splitter */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.3 }}
                    >
                        <MultiTPSplitter />
                    </motion.div>

                    {/* Execution Prison */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.4 }}
                    >
                        <ExecutionPrison />
                    </motion.div>
                </div>

                {/* Right Sidebar - Leaderboard */}
                <div className="h-full">
                    <TopTierLeaderboard />
                </div>
            </div>
        </div>
    );
};

export default GlamorismDashboard;
