"use client";
import React from 'react';
import { motion } from 'framer-motion';
import { Lock, TrendingUp, TrendingDown, Clock, Shield } from 'lucide-react';

interface LockedTrade {
    id: string;
    pair: string;
    side: 'BUY' | 'SELL';
    entry: number;
    current: number;
    sl: number;
    tp: number;
    lots: number;
    pnl: number;
    lockReason: string;
    unlockTime?: Date;
}

