"use client";
import React, { useEffect, useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Wifi, WifiOff, Zap } from 'lucide-react';

interface SyncStatusProps {
    isConnected: boolean;
    latency: number;
}

export const SyncStatusIndicator = ({ isConnected, latency }: SyncStatusProps) => {
    const [pulse, setPulse] = useState(true);

    useEffect(() => {
        const interval = setInterval(() => {
            setPulse(p => !p);
        }, 1000);
        return () => clearInterval(interval);
    }, []);

    const getLatencyColor = () => {
        if (latency < 20) return 'text-green-400';
        if (latency < 50) return 'text-yellow-400';
        return 'text-red-400';
    };

    const getLatencyGlow = () => {
        if (latency < 20) return 'glow-profit';
        if (latency < 50) return 'shadow-lg shadow-yellow-400/20';
        return 'glow-loss';
    };

    return (
        <AnimatePresence>
            <motion.div
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -20 }}
                className={`glass-glamor-heavy rounded-2xl px-4 py-3 border ${isConnected ? 'border-green-500/30' : 'border-red-500/30'
                    } ${isConnected ? 'glow-profit' : 'glow-loss'}`}
            >
                <div className="flex items-center gap-4">
                    {/* Connection Status */}
                    <div className="flex items-center gap-2">
                        <motion.div
                            animate={{ scale: pulse ? 1.2 : 1 }}
                            transition={{ duration: 1 }}
                        >
                            {isConnected ? (
                                <Wifi size={20} className="text-green-400" />
                            ) : (
                                <WifiOff size={20} className="text-red-400" />
                            )}
                        </motion.div>
                        <div>
                            <div className="text-xs font-bold text-white">MT5 Bridge</div>
                            <div className={`text-[10px] font-bold ${isConnected ? 'text-green-400' : 'text-red-400'}`}>
                                {isConnected ? 'CONNECTED' : 'DISCONNECTED'}
                            </div>
                        </div>
                    </div>

                    {/* Latency */}
                    {isConnected && (
                        <>
                            <div className="h-8 w-px bg-white/10" />
                            <div className="flex items-center gap-2">
                                <Zap size={16} className={getLatencyColor()} />
                                <div>
                                    <div className="text-xs font-bold text-white">Latency</div>
                                    <div className={`text-[10px] font-mono font-bold ${getLatencyColor()}`}>
                                        {latency}ms
                                    </div>
                                </div>
                            </div>
                        </>
                    )}

                    {/* Status Indicator */}
                    <div className="ml-auto">
                        <div className={`h-3 w-3 rounded-full ${isConnected ? 'bg-green-500' : 'bg-red-500'
                            } ${pulse ? 'animate-pulse' : ''}`} />
                    </div>
                </div>
            </motion.div>
        </AnimatePresence>
    );
};
